<template>
  <div id="detail">
    <detail-nab-bar
      class="detail-nab-bar"
      @titleClick="titleClick"
      ref="nav"
    ></detail-nab-bar>
    <scroll
      class="content"
      ref="scroll"
      :probe-type="3"
      @scroll="contentScroll"
    >
      <detail-swiper v-if="flag" :top-images="topImages"></detail-swiper>
      <detail-base-info :goods="goods"></detail-base-info>
      <detail-shop-info :shop="shop"></detail-shop-info>
      <detail-goods-info
        :detail-info="detailInfo"
        @imageLoad="imageLoad"
      ></detail-goods-info>
      <detail-param-info
        :param-info="paramInfo"
        ref="params"
      ></detail-param-info>
      <detail-comment-info
        :comment-info="commentInfo"
        ref="comment"
      ></detail-comment-info>
      <goods-list :goods="recommends" ref="recommend"></goods-list>
    </scroll>
    <detail-bottom-bar @addCart="addTOCart"></detail-bottom-bar>
    <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>
    <toast></toast>
  </div>
</template>

<script>
import DetailNabBar from "views/detail/childComps/DetailNabBar.vue";
import DetailSwiper from "views/detail/childComps/DetailSwiper.vue";
import DetailBaseInfo from "views/detail/childComps/DetailBaseInfo.vue";
import DetailShopInfo from "views/detail/childComps/DetailShopInfo.vue";
import DetailGoodsInfo from "views/detail/childComps/DetailGoodsInfo.vue";
import DetailCommentInfo from "views/detail/childComps/DetailCommentInfo.vue";
import DetailBottomBar from "views/detail/childComps/DetailBottomBar.vue";
import {
  getDetail,
  Goods,
  Shop,
  GoodsParam,
  getRecommend,
} from "network/detail";
import Scroll from "components/common/scroll/Scroll";
import DetailParamInfo from "views/detail/childComps/DetailParamInfo.vue";
import GoodsList from "components/content/goods/GoodsList.vue";
import BackTop from "components/content/backtop/BackTop.vue";
import { debounce } from "common//utils";
import Toast from "components/common/toast/Toast";

export default {
  name: "Detail",
  components: {
    DetailNabBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailGoodsInfo,
    DetailParamInfo,
    Scroll,
    DetailCommentInfo,
    GoodsList,
    DetailBottomBar,
    BackTop,
    Toast,
  },
  data() {
    return {
      iid: null,
      topImages: [],
      flag: false,
      goods: {},
      shop: {},
      detailInfo: {},
      paramInfo: {},
      commentInfo: {},
      recommends: [],
      themeTopYs: [],
      getThemeTopY: null,
      currentIndex: 0,
      isShowBackTop: false,
      message: "",
      ifShow: false,
    };
  },
  methods: {
    imageLoad() {
      this.$refs.scroll.refresh();
      // 点击标题滚应到对应内容
      // this.themeTopYs = [];
      // this.themeTopYs.push(0);
      // this.themeTopYs.push(this.$refs.params.$el.offsetTop);
      // this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
      // this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
      // console.log(this.themeTopYs);
      this.getThemeTopY();
    },
    titleClick(index) {
      this.$refs.scroll.scrollTo(0, -this.themeTopYs[index] + 44, 1000);
    },
    contentScroll(position) {
      const positionY = -position.y;
      for (let i = 0; i < this.themeTopYs.length; i++) {
        if (
          (this.currentIndex !== i &&
            i < this.themeTopYs.length - 1 &&
            positionY >= this.themeTopYs[i] &&
            positionY < this.themeTopYs[i + 1]) ||
          (i === this.themeTopYs.length - 1 && positionY >= this.themeTopYs[i])
        ) {
          this.currentIndex = i;
          this.$refs.nav.currentIndex = this.currentIndex;
        }
      }
      // 显示隐藏回到顶部按钮
      this.isShowBackTop = -position.y > 1000;
    },
    backClick() {
      this.$refs.scroll.scrollTo(0, 0, 300);
    },
    addTOCart() {
      // 1.获取购物车需要展示的信息
      const product = {};
      product.image = this.topImages[0];
      product.title = this.goods.title;
      product.desc = this.goods.desc;
      product.price = this.goods.realPrice;
      product.iid = this.iid;
      // 2.将商品加入购物车
      this.$store.dispatch("addCart", product).then((res) => {
        this.$toast.show("加入购物车成功");
        // this.message = res;
        // this.ifShow = true;
        // setTimeout(() => {
        //   this.ifShow = false;
        //   this.message = "";
        // }, 1500);
      });
    },
  },
  created() {
    // 1.保存传入的iid
    this.iid = this.$route.params.iid;
    // 2.根据iid请求详细数据
    getDetail(this.iid).then((res) => {
      console.log(res);
      const data = res.result;
      // 1.获取顶部轮播图数据
      this.topImages = data.itemInfo.topImages;
      // 2.获取商品信息
      this.goods = new Goods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      );
      // 3.获取店铺信息
      this.shop = new Shop(data.shopInfo);
      // 4.保存商品的详细数据
      this.detailInfo = data.detailInfo;
      // 5.获取参数的信息
      this.paramInfo = new GoodsParam(
        data.itemParams.info,
        data.itemParams.rule
      );
      // 6.取出评论信息
      if (data.rate.cRate !== 0) {
        this.commentInfo = data.rate.list[0];
      }

      // 防抖操作,点击标题滚应到对应内容
      this.getThemeTopY = debounce(() => {
        this.themeTopYs = [];
        this.themeTopYs.push(0);
        this.themeTopYs.push(this.$refs.params.$el.offsetTop);
        this.themeTopYs.push(this.$refs.comment.$el.offsetTop);
        this.themeTopYs.push(this.$refs.recommend.$el.offsetTop);
        console.log(this.themeTopYs);
      }, 500);

      this.flag = true;
    });

    // 1.请求推荐数据
    getRecommend().then((res) => {
      this.recommends = res.data.list;
    });
  },
};
</script>

<style scoped>
#detail {
  position: relative;
  z-index: 9;
  background-color: #fff;
  height: 100vh;
}
.content {
  height: calc(100% - 44px - 58px);
  overflow: hidden;
}
</style>